.App {
	background: #333;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.App video {
	/* display: none; */
	background: black;
}

.App .filter1 {
	-webkit-filter: url('red_filter.svg#redfilter1');
	        filter: url('red_filter.svg#redfilter1');
}

.App .filter2 {
	-webkit-filter: url('red_filter.svg#redfilter2');
	        filter: url('red_filter.svg#redfilter2');
}

.Center {
	width: 100vw;
	height: 100vh;
}

.Filters {
	list-style: none;
	margin: 0;
	padding: 0;
	position: fixed;
	top: 0;
	right: 0;
	z-index: 1;
}

.Filters li {
	font-size: 125%;
	background: #333;
	border: 1px solid;
	padding: 0.5em;
	border-radius: 1em;
	cursor: pointer;
	color: rgb(192,0,0);
	margin-top: 0.5em;
	margin-right: 0.5em;
	text-align: center;
	-webkit-transition: all 0.25s ease;
	-o-transition: all 0.25s ease;
	transition: all 0.25s ease;
	line-height: 1.5em;
	width: 1.5em;
	font-weight: bold;
}

.Filters li.selected {
	color: white;
	background: rgb(192,0,0);
	-webkit-box-shadow: 0 0 0.5em rgba(0,0,0,0.5);
	        box-shadow: 0 0 0.5em rgba(0,0,0,0.5);
}

.App .Message {
	position: fixed;
	bottom: 0;
	padding: 0;
	margin: 0;
	width: 100%;
	color: white;
}



